import { h, render, Component } from 'preact';
import classnames from 'classnames/dedupe';

import axios from 'axios';

import Loadable from 'react-loadable';
import Toolbar from 'q-mdc/elements/material-toolbar';
import Button from 'q-mdc/elements/material-button';
import LayoutGrid from 'q-mdc/elements/material-layout-grid';
import ToolbarRow from 'q-mdc/elements/material-toolbar-row';
import ToolbarSection from 'q-mdc/elements/material-toolbar-section';
import ToolbarTitle from 'q-mdc/elements/material-toolbar-title';
import Cookies from 'universal-cookie';

// import Hamburger from '../hamburger';

import { Menu as MenuIcon, Quikr, Back } from '../icons/headerIcons';
import showOrHide from '../hoc/showOrHide';
import MyAccountMenu from '../myAccount';

import City from '../city';
import { getCurrentCity, redirectToPostAd } from '../../helpers';

import '@material/typography/mdc-typography.scss';
import styles from './index.scss';


const regex = /(<([^>]+)>)/ig;
const cookies = new Cookies();
const cityCookieTTL = 60 * 60 * 24 * 30;
const cookieDomain = '.quikr.com';

const LoadableSearchPopup = Loadable({
	loader: () => import('../city'),
	loading: () => <div />,
	render(loaded, props){
		let CitySearchPopup = loaded.default;
		return <CitySearchPopup {...props} />;
	}
});

const LoadableHamburger = Loadable({
	loader: () => import('../hamburger'),
	loading: () => <div />,
	render(loaded, props){
		let Hamburger = loaded.default;
		return <Hamburger {...props} />;
	}
});

class Header extends Component {
	
	constructor(props) {
		super(props);
		this.state = {
			results: [],
			error: null,
			value: null,
			cityPopup: false,
			topCitiesList: false,
			drawerOpen: false
		};
	}
	
	handleCitySearch = (event) => {
		this.setState({
			topCitiesList: false
		});

		const { target: { value } } = event;
		
		let hostname = 'www.quikr.com';
		if (window && window.location && window.location.hostname) {
			hostname = window.location.hostname;
		}

		let params = new URLSearchParams();
		params.append('keyword', value);
		
		axios.post('https://feservices.quikr.com/v2/header/suggest_cities_json', params, {
			accept: 'application/json, text/javascript, */*; q=0.01',
			'accept-language': 'en-GB,en-US;q=0.9,en;q=0.8',
			'Content-Type': 'application/x-www-form-urlencoded'
		}).then((res) => {
			if (res.status === 200) {
				this.setState({
					results: res.data,
					err: null,
					value
				});
			}
			else {
				this.setState({
					results: [],
					err: 'Something went wrong',
					value
				});
			}
		}).catch((err) => {
			this.setState({
				err,
				results: [],
				value
			});
		});
	};

	showCityPopup = () => {
		this.setState({
			cityPopup: true,
			topCitiesList: true
		});
	};

	closeCityPopup = () => {
		this.setState({
			cityPopup: false,
			value: null,
			results: [],
			err: null
		});
	};

	openHamburger = (e) => {
		e.preventDefault();
		e.stopPropagation();
		e.nativeEvent.stopImmediatePropagation();
		this.setState({
			drawerOpen: true
		});
	}

	closeHamburger = () => {
		this.setState({
			drawerOpen: false
		});
	}

	handleBackOnClick = (e) => {
		const { handleBackClick } = this.props;
		const {  router: { history } } = this.context;
		if (handleBackClick) {
			handleBackClick(e, history);
		}
		else {
			history.goBack();
		}
	}

	render(props, state, context) {
		// can get the userInfo from context also this.context.user
		const { hasTitle = true, toolbarItems = true, hamburger = true, back = false, logoHref = null, myAccountMenu = {}, userInfo: user, searchCityCallback } = props;
		const { loggedIn: isLoggedIn } = user;
		
		const {  router: { history } } = context;
		let classes = classnames(styles.root, 'mdc-typography');
		
		const { placeholder = 'Search City' } = props;
		const { results, err, cityPopup, value, topCitiesList } = state;
		
		let gridArray = [];
		for (let i = 1; i <= 12; i++) {
			gridArray.push(<LayoutGrid.Cell desktopCols="1">{i}</LayoutGrid.Cell>);
		}
		return (
			<div class={classes}>
				<Toolbar>
					<ToolbarRow>
						<ToolbarSection start>
							{hamburger && <a class="mdc-toolbar__icon--menu"
								onClick={this.openHamburger}
							              >
								<MenuIcon class="mdc-toolbar__icons" />
							</a>}
							{ back && <a class="mdc-toolbar__icon--menu" onClick={this.handleBackOnClick}><Back class="mdc-toolbar__icons"  /></a>}
							<ToolbarTitle>
								<a href={logoHref ? logoHref : '/'} class="logo">
									<Quikr class="mdc-toolbar__icons" />
								</a>
								{hasTitle && <span onClick={this.showCityPopup}>
									{getCurrentCity()}
									{ <i class="mdc-toolbar__arrow" />}
								</span>}
				
							</ToolbarTitle>
			
						</ToolbarSection>
			
						{ typeof(toolbarItems) === typeof(true) && toolbarItems && <ToolbarSection end>
				
							<MyAccountMenu isLoggedIn={isLoggedIn} {...myAccountMenu} />
							<Button unelevated compact dense onClick={redirectToPostAd}> Post Free Ad </Button>
						</ToolbarSection>}
						{ toolbarItems.length > 0 && <ToolbarSection end>
							{toolbarItems}
						</ToolbarSection>}
					</ToolbarRow>
					{/* <Hamburger drawerOpen={state.drawerOpen} onClose={this.closeHamburger} isLoggedIn={isLoggedIn} user={user} /> */}
					{state.drawerOpen && <LoadableHamburger drawerOpen={state.drawerOpen} onClose={this.closeHamburger} isLoggedIn={isLoggedIn} user={user} />}
				</Toolbar>
				{cityPopup && <LoadableSearchPopup show={cityPopup} results={results} err={err} value={value} closePopup={this.closeCityPopup} placeholder={placeholder}
					handleSearch={this.handleCitySearch} showTopCities={topCitiesList} searchCityCallback={searchCityCallback}
				              />}
			</div>
		);
	}
}
	
export default showOrHide(Header);


