const globalContainer = (state = { visitor: {}, cityObject: {}, activities: { activities:[] } }, action ) => {
	switch (action.type) {
		case 'GET_RECENT_ACTIVITIES_SUCCESS':
			return Object.assign({},state,{ activities: { ...action.payload } });
		case 'GET_RECENT_ACTIVITIES_FAILURE':
			return Object.assign({},state,{ activities: {} });
		default:
			return state;
	}
};

export default globalContainer;


