const categories = (state = {}, action) => {
	switch (action.type){
		case 'GET_PWA_LINKS_SUCCESS':
			return Object.assign({},state, { ...action.payload } );
		case 'GET_PWA_LINKS_FAILURE':
			return {};
		default:
			return state;
	}
};

export default categories;


