import { h, Component } from 'preact';
import { BrowserRouter, Route, Switch, StaticRouter } from 'react-router-dom';
import axios from 'axios';

import Header from '../header';
import Search from '../search';
import Footer from '../footer';
import { DEFAULT_HOSTNAME, GET_USER_API_PATH } from '../../helpers/constants';

import Style from './index.scss';


const RouterWrapper = ({ children, path= '/', location }) => {
	if (typeof window !== 'undefined') {
		return (
		  <BrowserRouter>
				{children}
		  </BrowserRouter>
		);
	  }
	return (
		  <StaticRouter location={location} context={{}}>
			{children}
		  </StaticRouter>
	);
	  
};


/**
 * Shell - Wraps your app with Quikr's Header and Footer
 * It accepts a config array - you can vary the behaviour of header and footer based on routes
 * Example - View app.js
 *
 */
class Shell extends Component{

	constructor(props) {
		super(props);
		this.state = {
			userInfo: {
				loggedIn: false
			}
		};
	}

	componentDidMount() {
		this.getUser();
	}

	/**
	 * This method is called whenever the props or state changes
	 * @return {Object} Context of child components
	 */
	getChildContext() {
		let context = {};
		context.user = this.state.userInfo;
		return context;
	}

	resetUser = () => {
		this.setState({
			userInfo: {
				loggedIn: false
			}
		});
	}

	getUser = () => {
		let hostname = DEFAULT_HOSTNAME;
		if (window && window.location && window.location.hostname) {
			hostname = window.location.hostname;
		}
		
		axios.get(`https://${hostname}/${GET_USER_API_PATH}`).then( (res) => {
			if (res.status === 200) {
				this.setState({
					userInfo: res.data
				});
			}
			else {
				this.resetUser();
			}
		}).catch((err) => {
			this.resetUser();
		});
	}

	render(props, state, context) {

		const { config, children, location } = props;
		const { userInfo } = state;

		return (<RouterWrapper location={location} context={{}} >
			<div class="actual-app">
				<Switch>
					{config.map((c) => (<Route {...c.route} >
						<Header {...c.header} userInfo={userInfo} />
					</Route>))}
					<Route  >
						<Header show hasTitle userInfo={userInfo} />
					</Route>
				</Switch>
				<Switch>
					{config.map((c) => (<Route {...c.route} >
						<Search {...c.search} />
					</Route>))}
					<Route  >
						<Search show />
					</Route>
				</Switch>
				{children}
				<Switch>
					{config.map((c) => (<Route {...c.route} >
						<Footer {...c.footer} />
					</Route>))}
					<Route >
						<Footer show />
					</Route>
				</Switch>
			</div>
		</RouterWrapper>);

		
	}
}

export default Shell;


