import { h, render, Component } from 'preact';
import { Route, Switch, Link } from 'react-router-dom';
import classnames from 'classnames/dedupe';
import { Provider } from 'react-redux';
import axios from 'axios';
import Loadable from 'react-loadable';

import Shell from 'q-components/components/shell';
import Button from 'q-mdc/elements/material-button';
// import Search from 'q-components/components/search';
// import Homepage from './components/containers/entry/homepage';


// import asyncComponent from './components/containers/hoc/asyncComponent';


import reducer from './redux/reducers';
import configureStore from './redux/store';

import style from './common.scss';
import styles from './styles';


// Grab the state from a global variable injected into the server-generated HTML
let preloadedState;
// Allow the passed state to be garbage-collected
if (typeof window !=='undefined') {
	preloadedState = window.__PRELOADED_STATE__;
	delete window.__PRELOADED_STATE__;
}


let store = configureStore(preloadedState);

// const SearchPage = asyncComponent(() => import('q-components/components/search').then(module => module.default));
// const Example = asyncComponent(() => import('./components/containers/entry/example').then(module => module.default));

const LoadableSearch = Loadable({
	loader: () => import('q-components/components/footer'),
	loading: () => <div />,
	render(loaded, props){
		let Footer = loaded.default;
		return <Footer {...props} />;
	}
});

const LoadableHomePage = Loadable({
	loader: () => import('./components/containers/entry/homepage'),
	loading: () => <div />,
	render(loaded,props) {
		let Homepage = loaded.default;
		return <Homepage {...props} />;
	}
});

const exampleConfig = [
	{
		route: { path: '/xyz/:id',exact: false }, // Refer https://reacttraining.com/react-router/web/api/Route/path-string.
		header: {
			show: true,
			hasTitle: false,
			toolbarItems: [
				<Button key="0" unelevated compact dense="true" >List Your Institute</Button>
			]
		},
		search: { show: true, placeholder: 'Search in XYZ' },
		footer: { show: true }
	},
	{
		route: { path: '/jobs',exact: false },
		header: {
			hasTitle: true,
			show: true
		},
		search: { show: false, placeholder: 'Search in Jobs' },
		footer: { show: true }
	}
];


class App extends Component {

	componentDidMount(){
	}

	render(props,state) {
		let root = classnames('mdc-typography', styles.root);
		const { url } = props;
		return (
			<Provider store={store}>
				<Shell config={exampleConfig} gaID={'UA-15577634-7'} location={url} >
					<div class={root}>
						<Switch>
							<Route path="/xyz/123">
								<div>
								XYZ PAGE
									<Link to="/" >Home</Link>
								</div>
							</Route>
							<Route path="/">
								<LoadableHomePage show />
							</Route>
							<Route default >
								404 NOT FOUND
							</Route>
							
						</Switch>
					</div>
				</Shell>
			</Provider>);
	}
}
export default  App;
	


