import axios from 'axios';
import get from 'lodash/get';

const hasAxiosKey = val => val && typeof val.axios === 'boolean';
const handleAxios = val => val && val.axios === true;

const axiosPromiseMiddleware = store => next => (action) => {
	const { dispatch } = store;
	if (hasAxiosKey(action) && handleAxios(action)) {
		return new Promise((res, rej) => {
			axios(action.options)
				.then((response) => {
					const result = Object.assign({}, action, { payload: response.data, axios: false, type: `${action.type}_SUCCESS`, error: false });
					res(result);
					return dispatch(result);
				}).catch((error) => {
					const result = Object.assign({}, action, { payload: error, axios: false, type: `${action.type}_FAILURE`, error: true });
					// rej(result);
					if (typeof window !== 'undefined'){
						const Raven = get(window,'Raven', { captureException: () => {} });
						Raven.captureException('Axios Call Failed ',{ extra: { error, options: action.options } });
					}
					return dispatch(result);
				});
		});
	}
	return next(action);
};

export default axiosPromiseMiddleware;


