import { h, render, Component } from 'preact';
import Menu from 'q-mdc/elements/material-menu';
import { Account } from '../icons/headerIcons';

import styles from './index.scss';

const defaultLoggedOutMenuArr = [
	{  title: 'My Cart', href: '/QuikrX/Cart' },
	{ title: 'My Doorstep Offers', href: '/Escrow/MyOffers/getBuyersOfferForm' },
	{ title: 'My Chats', href: '/MyQuikr?action=mychats' },
	{ title: 'Login/Register', href: '/SignIn' }
];

const defaultLoggedInMenuArr = [
	{ href: '/MyQuikr',title: 'My Account' },
	{ href: '/MyQuikr?action=activeads',title: 'My Ads' },
	{  title: 'My Cart', href: '/QuikrX/Cart' },
	{ title: 'My Doorstep Offers', href: '/Escrow/MyOffers/getBuyersOfferForm' },
	{ href: '/MyQuikr?action=mychats', title: 'My Chats'/*<span class="notification--num" >3</span>*/ },
	{ href: '/MyQuikr?action=alertsdetails',title: 'My Alerts' },
	{ href: '/MyQuikr?action=lead',title: 'My Leads' },
	{ href: '/QuikrX/myorders',title: 'My Orders' },

	/* <Menu.Item link href="#">Recommendation for me</Menu.Item> */
	
	{ title: 'Login/Register', href: '/bye' }
];

class QMenu extends Component {
	render ( { list, refFn } ) {
		return (<Menu ref={menu => refFn(menu)}  links>{list.map((menuItem) => <Menu.Item link href={menuItem.href}>{menuItem.title}</Menu.Item> )}</Menu>);
	}
}

class MyAccountMenu extends Component{
	render(props){
		const { isLoggedIn , loggedInMenu = defaultLoggedInMenuArr , loggedOutMenu = defaultLoggedOutMenuArr } = props;
		return (
			<span>
				<a href="javascript:void(0);"
					class="mdc-toolbar__icon"
					onClick={e => {
						this.menu.MDComponent.open = true;
					}}
				>
					<Account class="mdc-toolbar__icons" />
				</a>
				
				{ !isLoggedIn && <QMenu refFn={menu => {
					this.menu = menu;
				}} list={loggedOutMenu}
				                 />  }

				{ isLoggedIn && <QMenu refFn={menu => {
					this.menu = menu;
				}} list={loggedInMenu}
				                />
				}

			</span>
		);
	}
}
export default MyAccountMenu;


