const initialState = [];

const banners = (state=initialState, action) => {
	switch (action.type){
		case 'GET_HOMEPAGE_BANNERS_SUCCESS':
			return Object.assign([], state, [...action.payload]);
		case 'GET_HOMEPAGE_BANNERS_FAILURE':
			return [];
		default:
			return state;
	}
};

export default banners;



