import { h, render, Component } from 'preact';
import Loadable from 'react-loadable';

import {  Search as SearchIcon, Shortlist } from '../icons/searchIcons';
import showOrHide from '../hoc/showOrHide';
import { getCurrentCity } from '../../helpers';
import { ALL_INDIA } from '../../helpers/constants';


// import Popup from '../pageLayer';
import styles from './index.scss';


const LoadableSearchPopup = Loadable({
	loader: () => import('./searchPopup'),
	loading: () => <div />,
	render(loaded, props){
		let SearchPopup = loaded.default;
		return <SearchPopup {...props} />;
	}
});


class Search extends Component {
	
	constructor(props) {
		super(props);
		this.state = {
			popup: false
		};
	}
	
	
	showPopup = () => {
		this.setState({
			popup: true
		});
	}
	
	closePopup = () => {
		this.setState({
			popup: false
		});
	}

	getPlaceholder = (prefix = 'Search') => `${prefix} In ${getCurrentCity()}`;
	
	render(props, state, context) {
		const {
			placeholder = this.getPlaceholder(),
			onSearchCallback,
			searchSuggestions,
			onSearchSubmit,
			onRecentSearchClick,
			onTrendingSearchClick,
			showTrendingSearches,
			showRecentSearches
		} = props;
		
		
		const { popup } = state;
		return (
			<div>
				<form class="toolbar-search">
					<div class="toolbar-search__child">
						<SearchIcon class="toolbar-search__icon" />
						<input class="toolbar-search__field" type="text" placeholder={placeholder} onClick={this.showPopup} />
						{/* <aside class="action-box">
							<i class="badge" data-badge="8">
								<Shortlist class="action-box__icons" />
							</i>
						</aside> */}
				
					</div>
				</form>
				{popup &&
					<LoadableSearchPopup
						show={popup}
						closePopup={this.closePopup}
						placeholder={placeholder}
						onSearchCallback={onSearchCallback}
						searchSuggestions={searchSuggestions}
						onSearchSubmit={onSearchSubmit}
						onRecentSearchClick={onRecentSearchClick}
						onTrendingSearchClick={onTrendingSearchClick}
						showRecentSearches={showRecentSearches}
						showTrendingSearches={showTrendingSearches}

					/>}
				
			</div>
		);
		
			
	}
}

export default showOrHide(Search);



