import { h, render, Component } from 'preact';

import Popup from '../pageLayer';
import Toolbar from 'q-mdc/elements/material-toolbar';
import ToolbarRow from 'q-mdc/elements/material-toolbar-row';
import ToolbarSection from 'q-mdc/elements/material-toolbar-section';
import ToolbarTitle from 'q-mdc/elements/material-toolbar-title';
import List from 'q-mdc/elements/material-list';
import ListItem from 'q-mdc/elements/material-list-item';

import { Back } from '../icons/headerIcons';
import axios from 'axios';
import { CITY_COOKIE_TTL, COOKIE_DOMAIN } from '../../helpers/constants';
import Cookies from 'universal-cookie';

import styles from '../search/suggester.scss';

const regex = /(<([^>]+)>)/ig;

const axiosInstance = axios.create({
	baseURL: 'https://www.quikr.com/',
	timeout: 1000,
	headers: {
		accept: 'application/json, text/javascript, */*; q=0.01',
		'accept-language': 'en-GB,en-US;q=0.9,en;q=0.8',
        'Content-Type': 'application/x-www-form-urlencoded'
	}
});
const cookies = new Cookies();

const topCityList = [
	{"name":"All India", id:"0"},
    {"name":"Ahmedabad", id:"22"},
    {"name":"Bangalore", id:"23"},
    {"name":"Chandigarh", id:"24"},
	{"name":"Chennai", id:"25"},
    {"name":"Coimbatore", id:"26"},
    {"name":"Delhi", id:"27"},
    {"name":"Hyderabad", id:"28"},
	{"name":"Kochi", id:"29"},
    {"name":"Kolkata", id:"30"},
    {"name":"Mumbai", id:"31"},
    {"name":"Pune", id:"33"},
    {"name":"Gurgaon", id:"132222"},
    {"name":"Jaipur", id:"152222"},
    {"name":"Lucknow", id:"162222"},
	{"name":"Noida", id:"201301"},
    {"name":"NaviMumbai", id:"400701"},
    {"name":"Ahmedabad", id:"695001"},
];

class citySearchPopup  extends Component {
    handleCitySearchClick = (cityId, cityName) => {
        const { searchCityCallback } = this.props;
        let {g_id} = this.props;
        g_id = g_id || 0;
    	cookies.set('new_prefer_city', cityName.toLowerCase(), {
    		path: '/',
    		maxAge: CITY_COOKIE_TTL,
    		domain: COOKIE_DOMAIN
    	});
    	cookies.set('prefer_city_id', cityId, {
    		path: '/',
    		maxAge: CITY_COOKIE_TTL,
    		domain: COOKIE_DOMAIN
    	});
        if (searchCityCallback) {
            searchCityCallback(cityId, cityName, g_id);
        }else {
            this.searchCityDefaultCallback(cityId, cityName, g_id);
        }
    };

    searchCityDefaultCallback = (cityId, cityName, g_id) =>{
    	let params = new URLSearchParams();
    	params.append('cityName', cityName);
    	params.append('categoryId', g_id);
    	params.append('gid','0, www');
    	params.append('URL','/');
        axiosInstance.post('/?aj=1&citysrch=true&sx=true&carsResponsiveLayout=1', params).then((res) => {
            if (res.status === 200) {
                let id = res.data.id;
                let path = window.location.pathname;
                let reg = /cId-[0-9]+|cId[0-9]+/;
                let regw = /w[0-9]+/;
                let regy = /y[0-9]+/;
                let regg = /gId-[0-9]+|gId[0-9]+/;
                let matchcId = path.match(reg);
                let matchwId = path.match(regw);
                let matchyId = path.match(regy);
                let matchgId = path.match(regg);
                let filterParams = window.location.search;
                if (matchcId != null) {
                    if (cityId == 0 || cityId == 1) {
                        let tmp_gid = matchcId[0].replace(/[0-9]+/, g_id).replace(/cId/, 'gId');
                        path = path.replace(reg, tmp_gid);
                    } else {
                        let tmp_cid = matchcId[0].replace(/[0-9]+/, id);
                        path = path.replace(reg, tmp_cid);
                    }
                } else if (matchcId == null && matchwId != null) {
                    if (cityId == 0 || cityId == 1) {
                        path = path.replace(regw, "y" + g_id);
                    } else {
                        path = path.replace(regw, "w" + id);
                    }
                } else if (matchyId != null) {
                    path = path.replace(regy, "w" + id);
                } else if (matchgId != null && !(cityId == 0 || cityId == 1)) {
                    let tmp_cid = matchgId[0].replace(/[0-9]+/, id).replace(/gId/, 'cId');
                    path = path.replace(regg, tmp_cid);
                }else {
                    path = "/";
                }
                let subDomain = (cityId != 0 && cityId != 1) ? cityName.toLowerCase() : "www";

                window.location = window.location.protocol+ '//' + subDomain + '.quikr.com' + path + filterParams;
                return;
            } else {
                console.log("Something went wrong");
            }
        }).catch((err) => {
            console.log("Something went wrong");
        });
	};


    render ({ show,value, placeholder,handleSearch, err, results, closePopup, showTopCities, closeTopCityList },state,context) {
    	return (<Popup show={show} >
    		<Toolbar>
    			<ToolbarRow>
    				<ToolbarSection start>
    					<a class="mdc-toolbar__icon--menu" onClick={closePopup}>
    						<Back class="mdc-toolbar__icons" />
    					</a>


    					<ToolbarTitle>
    						<div class="toolbar-search">
    							<input class="toolbar-search__field" value={value} autoFocus type="text" placeholder={placeholder} onChange={handleSearch}
    								onInput={handleSearch}
    							/>
    						</div>
    					</ToolbarTitle>

    				</ToolbarSection>

    			</ToolbarRow>
    		</Toolbar>
    		<div class="search__suggester">
                {showTopCities && <div class="toolbar-search__suggestions">
                    <List>
                        {topCityList.map((v) =>
                            (<ListItem>
                                    <p onClick={() => this.handleCitySearchClick(v.id, v.name)}>
                                        {`${v.name}`}
                                    </p>
                                </ListItem>
                            ))}
                    </List>
                </div>}
    			{err === null && results.length !== 0 && (<div class="toolbar-search__suggestions">
    				<List>
    					{results.map((v) =>
    						(<ListItem>
    							<p onClick={() => this.handleCitySearchClick(v.id, v.name)}>
    								{`${v.name}`}
    							</p>
    						</ListItem>
    						))}
    				</List>
    			</div>)}
    			{/* results.length === 0 && <Suggester />  */}
    		</div>
    	</Popup>);
    }
}

export default citySearchPopup;



